a {
  text-decoration: none;
  color: black;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border: 1px solid black;
  margin-bottom: 10px;
  transition: 0.5s linear;
}
a:hover {
  background-color: black;
  color: white;
}

li {
  list-style-type: none;
}
