body {
  margin: 0;
  padding: 0;
}

.header {
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #34495e;
  padding: 0;

  margin: 0;
  height: 100px;
}
ul {
  padding-left: 0;
  margin: 0 30px;
}

.header li {
  list-style-type: none;
}

.header a {
  text-decoration: none;
  color: white;
  font-size: 1.25rem;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  border: 1px solid white;
  transition: 0.5s linear;
  padding: 10px;
}

.header a:hover {
  background-color: white;
  color: black;
}

@media screen and (max-width: 512px) {
  .header a {
    font-size: 1rem;
    width: 70px;
    height: 40px;
  }
}
